import * as _ from 'lodash';
import { Force } from './force.d3';
import { IActionModel } from '../../../directives/action/models/action.model';

export class D3Controller {
    static $inject = ["$stateParams", "fxAction"];

    actionModel: IActionModel;
    private key: string;

    constructor(private $stateParams: ng.ui.IStateParamsService, private fxAction) {
        // new Force("#paged3");
        this.key = $stateParams["key"];
        this.doInit();
    }

    doInit() {
        this.fxAction.getModel(this.key).then((actionModel) => {
            this.actionModel = actionModel;
        })
    }
}


